import React, { Component } from 'react'
import {Select, Input, Button} from 'antd';
// import {postAction} from "src/axios";
const Option = Select.Option;

export default class Filter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // 
            schoolYear:'',
            schoolYearLists:[],
            // 
            department:'',
            departmentLists:[],
            // 
            title:'',
            titleLists:[],
            // 
            education:'',
            educationLists:[],
            
            jobNumber:'',
            name:'',
        };
    }
    componentDidMount(){
        this.getSchoolYearLists();
        this.getDepartmentLists();
        this.getTitleLists();
        this.getEducationLists();

    }
    // list
    getSchoolYearLists() {
        this.setState({
            schoolYearLists: [
                {name: '2019', value: '2019'},
                {name: '2018', value: '2018'},
                {name: '2017', value: '2017'},
            ]
        })
        // postAction(
    	// 	"/bigdata/school/listClassByMajor",
    	// 	{
    	// 		year: this.state.curGrade,
    	// 		majorCode: this.state.curMajor
    	// 	}
    	// ).then(
    	// 	(res) => {
    	// 		if (res.success){
    	// 			this.setState({
    	// 				classLists: res.obj,
    	// 			})
    	// 		}
    	// 	}
    	// )
    }
    // list
    getDepartmentLists() {
        this.setState({
            departmentLists: [
                {name: '1', value: '1'},
                {name: '2', value: '2'},
            ]
        })
    }
    // list
    getTitleLists() {
        this.setState({
            titleLists: [
                {name: '1', value: '1'},
                {name: '2', value: '2'},
            ]
        })
    }
    // list
    getEducationLists() {
        this.setState({
            educationLists: [
                {name: '1', value: '1'},
                {name: '2', value: '2'},
            ]
        })
    }

    changeFilter = (num,value)=>{
        if(num===0){//
            this.setState({
                schoolYear:value
            })
        }else if(num === 1){
            this.setState({
                department:value
            })
        }else if(num === 2){
            this.setState({
                title:value
            })
        }else if(num === 3){
            this.setState({
                education:value
            })
        }
    }
    changeInputValue(num,e){
        if(num===0){
            this.setState({jobNumber: e.target.value});
        }else if(num===1){
            this.setState({name: e.target.value});
        }
        
    }
    // 
    sureBtn = () => {
        let {schoolYear,department,title,education,jobNumber,name} = this.state;
    	let filterValue={
    		schoolYear,
    		department,
    		title,
            education,
            jobNumber,
            name
    	}
    	
    	this.props.getFilterValue(filterValue);
    }
    render() {
        return (
            <div style={{padding: '10px 20px', lineHeight: '40px', background: 'rgba(255,255,255,0.6)'}}>
               <span></span> 

               <Select 
                	value={this.state.schoolYear}
                	placeholder="" 
                	style={{ width: 120, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 0)}
                >
					<Option value=''></Option>
                    {
                        this.state.schoolYearLists.map((item, index) => <Option key={index} title={item.name}   value={item.value}>{item.name}</Option>)
                    }
                </Select>
                <Select 
                	value={this.state.department}
                	placeholder="" 
                	style={{ width: 180, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 1)}
                >
					<Option value=''></Option>
                    {
                        this.state.departmentLists.map((item, index) => <Option key={index} title={item.name}   value={item.value}>{item.name}</Option>)
                    }
                </Select>
                <Select 
                	value={this.state.title}
                	placeholder="" 
                	style={{ width: 120, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 2)}
                >
					<Option value=''></Option>
                    {
                        this.state.titleLists.map((item, index) => <Option key={index} title={item.name}   value={item.value}>{item.name}</Option>)
                    }
                </Select>
                <Select 
                	value={this.state.education}
                	placeholder="" 
                	style={{ width: 120, marginRight: 10 }} 
                	onChange={this.changeFilter.bind(this, 3)}
                >
					<Option value=''></Option>
                    {
                        this.state.educationLists.map((item, index) => <Option key={index} title={item.name}   value={item.value}>{item.name}</Option>)
                    }
                </Select>

                <Input style={{ width: 180, marginRight: 10 }} value={this.state.jobNumber} onChange={this.changeInputValue.bind(this,0)} placeholder=""></Input>
                <Input style={{ width: 180, marginRight: 10 }} value={this.state.name} onChange={this.changeInputValue.bind(this,1)} placeholder=""></Input>
                
                <Button type="primary" icon="search" onClick={this.sureBtn}>  </Button>
            </div>
        )
    }
}
